.component-grid-square {
  position: relative;
  border: 1px solid black;
  box-sizing: border-box;
}

.orange {
  background: orange;
}

.component-grid-square.white {
  background: white;
  color: black;
}

.component-grid-square.black {
  background: black;
  color: white;
}

.component-grid-square::before {
  content: "";
  display: block;
  padding-top: 100%;
}

.component-grid-square .content {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.text-container {
  width: 80%;
  height: 80%;
  padding-top: 10%;
  padding-left: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.text {
  font-size: 25px;
}

input {
  font-size: 30px;
  border: none;
  outline: none;
}

.single-digit {
  width: 18px;
  height: 100%;
}

.double-digit {
  width: 100%;
  height: 100%;
}

.operation {
  width: 100%;
  padding: 35%;
}

.operation img {
  width: 100%;
}

.component-grid-square.black.green-text {
  color: LightGreen;
}

.component-grid-square.black.red-text {
  color: Tomato;
}

/* small screen media queries */

@media only screen and (max-width: 500px) {
  .text {
    font-size: 20px;
  }

  input {
    font-size: 24px;
  }

  .single-digit {
    width: 16px;
  }
  
}

@media only screen and (max-width: 400px) {
  .text {
    font-size: 15px;
  }

  input {
    font-size: 18px;
  }

  .single-digit {
    width: 12px;
  }
}

@media only screen and (max-width: 300px) {
  .text {
    font-size: 10px;
  }

  input {
    font-size: 13px;
  }

  .single-digit {
    width: 9px;
    height: 9px;
  }
}